import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// Auth 
import { CanActivate } from '@angular/router';
import { AuthService as AuthGuard } from "./auth.service";
import { AuthRoleService as AuthRole } from "./auth-role.service";

import { HeroListComponent } from './hero-list/hero-list.component';
import { HeroDashboardComponent } from './hero-dashboard/hero-dashboard.component';

import { LoginformComponent } from './login/loginform/loginform.component';
import { TemplateComponent } from './pimpinan/template/template.component';
import { DashboardComponent } from './pimpinan/dashboard/dashboard.component';
import { MarketingComponent } from './pimpinan/marketing/marketing.component';
import { TemplateGudangComponent } from './gudang/template-gudang/template-gudang.component';
import { DashboardGudangComponent } from './gudang/dashboard-gudang/dashboard-gudang.component';
import { TestPointComponent } from './test-point/test-point.component';

const routes: Routes = [
  {path:'login', component:LoginformComponent},
  {path:'testpoint', component:TestPointComponent},
  { path: '',   redirectTo: '/login', pathMatch: 'full' },

  { path:'pimpinan', component:TemplateComponent, canActivate: [AuthRole],
  data: { 
    expectedRole: '1'
  },
    children:[
      { path:'dashboard', component:DashboardComponent },
      { path:'marketing', component:MarketingComponent }
    ]
  },

  { path:'gudang', component:TemplateGudangComponent, canActivate: [AuthRole],data: { 
    expectedRole: '2'
  },
    children:[
      { path:'dashboard', component:DashboardGudangComponent },
      { path:'marketing', component:MarketingComponent }
    ]
  },

  {path:'cek_data', component:HeroListComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
