import { Injectable } from '@angular/core';
import { 
  Router,
  CanActivate,
  ActivatedRouteSnapshot
} from '@angular/router';
import { AuthValidService } from './auth-valid.service';
import { JwtHelperService } from '@auth0/angular-jwt';


@Injectable({
  providedIn: 'root'
})
export class AuthRoleService implements CanActivate {

  constructor(public router: Router, public authValid:AuthValidService) { }

  canActivate(route: ActivatedRouteSnapshot): boolean{
    // this will be passed from the route config
    // on the data property
    const expectedRole = route.data.expectedRole;
    const token = localStorage.getItem('token');
    // decode the token to get its payload
    const helperDecode = new JwtHelperService();
    const tokenPayload = helperDecode.decodeToken(token);

    if (
      !this.authValid.isAuthenticated() || 
      tokenPayload.idn !== expectedRole
    ) {
      this.router.navigate(['/login']);
      console.log("role gagal");
      return false;
    }
    console.log(tokenPayload.idn+" role berhasil");
    return true;
    
  } 
}
